<template>
  <b-container class="relevanteFactorenWrapper">
    <b-row v-for="(effectKey, index) in displayEffecten" :key="effectKey" class="effectRow">
        <b-col>
            <p class="effectName"><em>{{ effecten[effectKey] }}</em></p>
            <b-row>
              <b-col cols="4">
              <vue-svg-gauge
              :start-angle="-110"
              :end-angle="110"
              :value="totalScores[effectKey]"
              :separator-step="0"
              :min="0"
              :max="8"
              :inner-radius="60"
              :gauge-color="gaugeColor"
              :scale-interval="1"
            >
            <div class="inner-text">
              <span>{{ totalScoresCategories[effectKey] }}</span>
            </div>
          </vue-svg-gauge>
              </b-col>
              <b-col cols="8" v-if="totalScoresOpbouw[effectKey].length > 0" >
                <b-row class="reason" v-for="(reason, index) in totalScoresOpbouw[effectKey]" :key="`${effectKey}-${index}`">
                  <div v-if="reason[2] != 1.">
                    <b-icon-patch-plus-fill class="plus" v-if="reason[2] > 1.0"></b-icon-patch-plus-fill> 
                    <b-icon-patch-minus-fill class="minus" v-else></b-icon-patch-minus-fill> 
                    <b>{{ translateFactorString(reason[0]) }}:</b> {{ reason[1] }}
                  </div>
                </b-row>
          </b-col>
          <b-col cols="8" v-else><em>Geen relevante factoren om te tonen.</em></b-col>
        </b-row>
          <hr v-if="index < 3">
        </b-col>
      </b-row>
  </b-container>

</template>

<script>
  import { VueSvgGauge } from 'vue-svg-gauge'
  
  export default {
    components: {
      VueSvgGauge,
    },
    props: {
      effecten: Object,
      displayEffecten: Array,
      totalScores: Object,
      totalScoresOpbouw: Object,
      totalScoresCategories: Object,
    },
    data() {
      return {
        gaugeColor: [
          {offset: 0,  color: '#B3FF78'},
          {offset: 12.5,  color: '#B3FF78'},
          {offset: 42, color: '#F2EE78'},
          {offset: 70, color: '#FF8441'},
          {offset: 100, color: '#FF0F00'},
        ],
      }
    },
    methods: {
      capitaliseString(string) {
        return string.charAt(0).toUpperCase() + string.slice(1);
      },
      translateFactorString(string) {
        return this.capitaliseString(string.replace("--", " | ").replace("-", " "));
      },
    },
  }
  
  </script>

