<template>
  <b-container>
    <b-row fluid class="functieTabelContainer">
      <!-- Functie tabel controls -->
      <b-col>
        <b-row v-if="opgeslagenFuncties.length > 0">
          <b-col class="d-flex align-items-center mx-0 px-0">
            <p class="my-0 py-0 mx-0 px-0">Klik op een rij in de tabel om de relevante factoren voor de betreffende functie analyse te bekijken.</p>
          </b-col>
          <b-col cols="4" class="d-flex flex-row-reverse">
            <b-button class="saveDelButton" size="sm" variant="danger" @click="deleteAll()">
              Verwijder alle functies
            </b-button>
            <b-button class="saveDelButton" size="sm" variant="success" @click="downloadData()">
              Download resultaten
            </b-button>
          </b-col>
        </b-row>
      </b-col>
      
      <!-- Functie tabel -->
      <b-container v-if="opgeslagenFuncties.length > 0" class="functieTabel shadow-sm text-left text-wrap">
        <b-row class="functieTabelHeader">
          <b-col cols="2">
            Functie
          </b-col>
          <b-col cols="2" v-for="effect in displayEffecten" :key="effect">
            {{ effecten[effect] }}
          </b-col>
          <b-col>
            Conclusie
          </b-col>
        </b-row>
        <b-row v-for="(functie, index) in opgeslagenFuncties" :key="index">
          <b-col>
            <b-row @click="setModalData(index); $bvModal.show('relevante-factoren-modal-2')"
              class="functieTabelRow text-left">
              <b-col cols="2">
                <svg @click="deleteEntry(index)" xmlns="http://www.w3.org/2000/svg" width="16" height="16"
                  fill="currentColor" class="bi bi-trash3-fill my-icon" viewBox="0 0 16 16">
                  <path
                    d="M11 1.5v1h3.5a.5.5 0 0 1 0 1h-.538l-.853 10.66A2 2 0 0 1 11.115 16h-6.23a2 2 0 0 1-1.994-1.84L2.038 3.5H1.5a.5.5 0 0 1 0-1H5v-1A1.5 1.5 0 0 1 6.5 0h3A1.5 1.5 0 0 1 11 1.5Zm-5 0v1h4v-1a.5.5 0 0 0-.5-.5h-3a.5.5 0 0 0-.5.5ZM4.5 5.029l.5 8.5a.5.5 0 1 0 .998-.06l-.5-8.5a.5.5 0 1 0-.998.06Zm6.53-.528a.5.5 0 0 0-.528.47l-.5 8.5a.5.5 0 0 0 .998.058l.5-8.5a.5.5 0 0 0-.47-.528ZM8 4.5a.5.5 0 0 0-.5.5v8.5a.5.5 0 0 0 1 0V5a.5.5 0 0 0-.5-.5Z" />
                </svg>
                {{functie.functieNaam}}
              </b-col>
              <b-col cols="2" v-for="effect in displayEffecten" :key="effect">
                {{ functie.totalScoresCategories[effect]}}
              </b-col>
              <b-col>
                {{ functie.conclusion }}
              </b-col>
            </b-row>

          </b-col>
        </b-row>
      </b-container>
      <b-container v-else class="geen-functies">
        <em>Er zijn nog geen functies geanalyseerd.</em>
      </b-container>
    </b-row>

    <!-- Functie analyse modal (laat de relevante factoren voor een gekozen functie rij zien) -->
    <b-modal size="lg" id="relevante-factoren-modal-2" ref="relevante-factoren-modal-2" no-footer
      :title="modalFunctieNaam">
      <RelevanteFactorenModal :effecten="effecten" :progressDonePercentage="donePercentage"
        :displayEffecten="displayEffecten" :totalScores="modalTotalScores" :totalScoresOpbouw="modalTotalScoresOpbouw"
        :totalScoresCategories="modalTotalScoresCategories"></RelevanteFactorenModal>
    </b-modal>
  </b-container>
</template>

<script>
import { centralStore } from '@/stores/main'
import RelevanteFactorenModal from './RelevanteFactorenModal.vue';
import { ExportToCsv } from 'export-to-csv';
import json from '../assets/ind_factor_weights_and_hierarchy.json'

export default {
  props: ['donePercentage'],
  components: { RelevanteFactorenModal },
  data() {
    return {
      weights: undefined,
      opgeslagenFuncties: [],
      modalFunctieNaam: "",
      modalTotalScores: {},
      modalTotalScoresOpbouw: {},
      modalTotalScoresCategories: {},
      displayEffecten: [
        'agressieve-bejegening',
        'doelgerichte-benadering',
        'corruptie,-omkoping,-fraude-gevoeligheid',
        'psychosociale-overbelasting'
      ],
      effecten: {
        'agressieve-bejegening': 'Agressieve bejegening',
        'doelgerichte-benadering': 'Doelgerichte benadering',
        'corruptie,-omkoping,-fraude-gevoeligheid': 'Corruptie-, omkoping- en fraudegevoeligheid',
        'psychosociale-overbelasting': 'Psychologische overbelasting',
        'overall': "Overall vermenigvuldigingsfactor",
      },
    }
  },
  mounted() {
    this.weights = json.weights;
    this.opgeslagenFuncties = this.store.functies;

    this.store.$subscribe((mutation, state) => {
      this.opgeslagenFuncties = state.functies;
    });
  },
  setup() {
    const store = centralStore()

    return {
      store,
    }
  },
  methods: {
    setModalData(index) {
      // Zet de informatie in de modal correct voor de te bekijken functie
      this.modalFunctieNaam = this.opgeslagenFuncties[index].functieNaam;
      this.modalTotalScores = this.opgeslagenFuncties[index].totalScores;
      this.modalTotalScoresOpbouw = this.opgeslagenFuncties[index].totalScoresOpbouw;
      this.modalTotalScoresCategories = this.opgeslagenFuncties[index].totalScoresCategories;
    },
    downloadData() {
      // Maak gebruik van CSV exporter om de JSON data als CSV te downloaden
      const options = {
        fieldSeparator: ';',
        decimalSeparator: ',',
        filename: "kwetsbaarheidanalyse-functies-personele-veiligheid",
        showLabels: true,
        showTitle: false,
        title: 'Kwetsbaarheidanalyse personele veiligheid',
        useTextFile: false,
        useBom: true,
        useKeysAsHeaders: true,
      }

      let exportData = this.makeExportData();

      const csvExporter = new ExportToCsv(options);
      csvExporter.generateCsv(exportData);
    },
    makeExportData() {
      // Maak JSON structuur (lijst van objecten) die de analyses per functie in zich hebben
      let exportData = [];

      for (let i = 0; i < this.opgeslagenFuncties.length; i++) {
        let func = this.opgeslagenFuncties[i];
        let row = { "Index": i };
        row["Functie"] = func.functieNaam;
        row["0 Uitkomst | totaal conclusie"] = func.conclusion;
        row["0 Uitkomst | totaal score"] = Number.parseFloat(func.overallScore);
        for (const [effect, effectDisplay] of Object.entries(this.effecten)) {
          row[`0 Uitkomst | ${effectDisplay}`] = Number.parseFloat(func.totalScores[effect]);
        }

        let tableData = this.makeTableData(func.currentVarChoices)[0];
        row = { ...row, ...tableData };

        exportData.push(row);
      }

      return exportData;
    },
    deleteAll() {
      // Reset store (functies wissen)
      this.store.$reset();
    },
    deleteEntry(index) {
      // Verwijder 1 entry
      this.store.$patch((state) => { state.functies.splice(index, 1); })
    },
    strHelp(s) {
      // Make longer strings into strings of specific format 
      // (lowercase and space replaced with hyphens) 
      // "Test string" -> "test-string"
      return s.replaceAll(" ", "-").toLowerCase()
    },
    makeTableData(currentVarChoicesX) {
      let newCurrentVarChoicesX = {};
      let id = 1;

      // Loop over indicator-factor (k) = [...values] (v)
      for (const [k, v] of Object.entries(currentVarChoicesX)) {

        // If there are variables selected
        if (v.length > 0) {
          newCurrentVarChoicesX[`${id} ${this.translateFactorString(k)}`] = "";

          // Loop over all selected variables
          for (let j = 0; j < v.length; j++) {
            let vsub = v[j];

            if (j == 0) {
              newCurrentVarChoicesX[`${id} ${this.translateFactorString(k)}`] = newCurrentVarChoicesX[`${id} ${this.translateFactorString(k)}`].concat(this.translateFactorString(vsub));
            } else {
              newCurrentVarChoicesX[`${id} ${this.translateFactorString(k)}`] = newCurrentVarChoicesX[`${id} ${this.translateFactorString(k)}`].concat(`, ${this.translateFactorString(vsub)}`);
            }

            for (const effect of Object.keys(this.effecten)) {
              if (j == 0) {
                newCurrentVarChoicesX[`${id} factor effect(en) op ${effect}`] = `${this.weights[k][this.strHelp(vsub)][effect]}`;
              } else {
                newCurrentVarChoicesX[`${id} factor effect(en) op ${effect}`] = newCurrentVarChoicesX[`${id} factor effect(en) op ${effect}`].concat(`, ${this.weights[k][this.strHelp(vsub)][effect]}`);
              }
            }
          }

          // If nothing is selected
        } else {
          newCurrentVarChoicesX[`${id} ${this.translateFactorString(k)}`] = "N/A";

          for (const effect of Object.keys(this.effecten)) {
            newCurrentVarChoicesX[`${id} factor effect(en) op ${effect}`] = "1.0";
          }
        }
        id += 1;
      }
      return [newCurrentVarChoicesX];
    },
    capitaliseString(string) {
      // Capitalise first character of string
      return string.charAt(0).toUpperCase() + string.slice(1);
    },
    translateFactorString(string) {
      // Format change:
      // "Factor--Variable name" -> "Factor | variable"
      return this.capitaliseString(string.replace("--", " | ").replace("-", " "));
    }
  }
}
</script>

<style scoped>
.functieTabelContainer {
  padding: 10px;
}

.functieTabelRow {
  padding-top: 12px;
  padding-bottom: 12px;
